import React from 'react'
import "react-responsive-carousel/lib/styles/carousel.min.css";

import logo1 from '../../assets/img/main_module1/1-47.jpg'
import logo2 from '../../assets/img/main_module1/xephang2022_vn3-33.jpg'
import logo3 from '../../assets/img/main_module1/harvard-mit-dtu-compressor-24-32-49.jpg'
import logo4 from '../../assets/img/main_module1/huan-chuong-lao-dong-dtu-compressor-10-81.jpg'
import { Carousel } from 'react-bootstrap';
import './CarouselBanner.css'
import BoxSlide from '../BoxSlide/BoxSlide';
const Test = () => {
    const carousel = [
        {
            id: 1,
            img: logo1,
            title: 'Cc ngnh hc "hot" ti i hc Duy Tn',
            description: 'Hc ngnh no  vic hc lun n nh d cho dch bnh c ti din hay khng?, Hc ngnh no ...',

        },
        {
            id: 2,
            img: logo2,
            title: 'i hc Duy Tn vo Top 500 Th gii theo bng Times Higher Education (THE) 2023',
            description: 'T chc Times Higher Education (THE) va cng b bng xp hng cc trng i hc tt nht th gii...',

        },
        {
            id: 3,
            img: logo3,
            title: 'Sinh vin Duy Tn v Bn lnh Chinh phc Cup V ch cc Cuc thi Quc gia v Quc t',
            description: 'Khng ch c bit n l i hc uy tn vi nhng thnh tu ng trn trng trong cng tc o...',

        },
        {
            id: 4,
            img: logo4,
            title: 'i hc Duy Tn t cc Hun chng Lao ng hng Nht - Nh',
            description: 'Trong qu trnh xy dng v pht trin, i hc Duy Tn  t c nhiu thnh tu trong cng tc...',

        }
    ]

    

    return (
     
            <Carousel
                controls={false}
                fade={true}
                interval={3000}
                width={'100%'}
               
                wrap={true}
                indicators={false}
                style={{ position: 'relative', height: 800 + 'px'}}
            >
                {carousel.map(review => (
                    <Carousel.Item key={review.id} style={{height: 800 + 'px'}}>
                        <img
                            src={review.img}
                            style={{width: '100%', height: '100%'}}
                            
                        />
                      
                        <Carousel.Caption className='carousel-banner-caption' >
                            <h3 className='box-news-title'>{review.title}</h3>
                            <p className='box-news-description'>{review.description}</p>
                            <a className='btn-read-more'>Xem thm...</a>
                        </Carousel.Caption>

                    </Carousel.Item>
                ))}
            
            </Carousel>


 
    )
}

export default Test