import React from 'react'
import './BoxSlide.css'
import imgAdmissions from '../../assets/img/box_slide/box_slide.jpg'
import imgEvent from '../../assets/img/box_slide/z4049676016363_e591e0c0fbce75bf22882624d44bae90-16.png'
import studentAward from '../../assets/img/box_slide/z4027991742754_8cd429365b82c9e1b4fb5f2d5bbc56bd-37.jpg'
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const BoxSlide = () => {
    // to bin cha slide
    const infoSlide = [
        {
            id: 1,
            image: imgEvent,
            title: ' 10 s kin ni bt ca H Duy Tn trong nm 2022',
            date: '19/01/2023',
            description: 'Mi mt nm qua i, mong c ca nhng ngi ng trn ging ng i hc l lm c tht nhiu iu c ch  ng gp vo tin trnh nng cao cht lng o ...'
        },
        {
            id: 2,
            image: studentAward,
            title: 'N sinh Khoa Y i hc Duy Tn nhn Gii thng "Sao thng Ging" 2022',
            date: '16/01/2023',
            description: 'Vi im trung bnh hc tp 3.91/4.00 cng nhng n lc khng ngng trong cc phong tro on v Hi, c nng B th Trn Th Phng -  xut sc tr thnh 1 trong ...'
        },
        {
            id: 3,
            image: 'https://cdn.duytan.edu.vn/news/uploads/media/408_256/images/bia1-68.png',
            title: 'Hai sinh vin H Duy Tn ginh 3 HCV ti i hi Th thao ton quc 2022',
            date: '15/01/2023',
            description: 'Ph v nhng k lc  tng c xc lp trc , hai sinh vin ca i hc (H) Duy Tn l knh ng Nguyn Hu Kim Sn v VV in kinh Nguyn Phm Hoi Yn  ...'
        },
        {
            id: 4,
            image: 'https://cdn.duytan.edu.vn/news/uploads/media/408_256/images/bia-63.png',
            title: "Cu sinh vin i hc Duy Tn l cha  ca 'hoa hu mo' 2023",
            date: '01/02/2023',
            description: 'ng mt nm trc  (Tt Nhm Dn 2022), anh Tm cng  nhn n t hng t chnh quyn huyn Triu Phong  to tc ra linh vt h. Ngay lp tc ch h vi vc ...'
        },
        {
            id: 5,
            image: 'https://cdn.duytan.edu.vn/news/uploads/media/408_256/images/biab-41.png',
            title: 'o to Y-Dc-iu dng ti H Duy Tn vi s an tm v cht lng',
            date: '21/01/2023',
            description: 'D l c s o to Y-Dc vi hn 15 nm kinh nghim, nhng Trng Y-Dc (CMP), H Duy Tn  to dng uy tn trong hot ng o to, thu ht ng o cc th ...'
        },
        {
            id: 6,
            image: 'https://cdn.duytan.edu.vn/news/uploads/media/408_256/images/%E1%BA%A2nh-VinIF-1-39.png',
            title: 'Cc ging vin, nh nghin cu ca H Duy Tn nhn hc bng ca qu VinIF',
            date: '19/01/2023',
            description: 'Bc s, ging vin Hunh L Thi Bo v ThS. Nguyn Duy Khi cng ThS. Nguyn Hunh Kim Ngn (n t Vin Nghin cu Khoa hc C bn v ng dng - IFAS ca i hc ...)'
        }
    ]
    
    
    let settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 3,
        slidesToScroll: 1,
        initialSlide: 0,
        autoplay: true,
        autoplaySpeed: 4000,
        
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 3,
                    
                    infinite: true,
                    dots: true
                }
            },
            {
                breakpoint: 987,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 2,
                    initialSlide: 2
                }
            },
           
        ]
    };
    return (

        <div className='box-slide-container'>
            <div className='container'>
                <div className='row-section' style={{ display: 'flex', flexWrap: 'wrap', position: 'relative' }}>
                    <div className='box-col-1'>
                        <img src={imgAdmissions} />
                    </div>
                    <div className='box-col-2'>

                        <Slider {...settings} >
                            {infoSlide.map(item => (
                                <div key={item.id}>
                                    <div style={{ padding: '6px 6px 10px',background: '#F8F8F8', borderRight: '1px solid #e0e0e0', height: '398px' }}>
                                        <img src={item.image} style={{ width: 259.66 + 'px', height: 162.93 + 'px' }} className='img-slide'/>
                                        <div className='slide-title'>{item.title}</div>
                                        <div className='slide-date'>{item.date}</div>
                                        <p className='slide-description'>{item.description}</p>
                                        <div className='social' >
                                            <i className="fab fa-facebook-square"></i>
                                            <i className="fab fa-twitter"></i>
                                            
                                        </div>
                                    </div>

                                </div>
                            ))}
                        </Slider>


                    </div>
                </div>

            </div>


        </div>
    )
}

export default BoxSlide